@media all and (min-width: 1024px) and (max-width: 1280px) {
  html,
  body,
  #root {
    height: 100%;
  }
  .contenu {
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    position: relative;
  }
  .letter_bar {
    display: flex;
    border: 2px solid black;
    margin-top: 100px;
  }
  .letter_box {
    line-height: 100px;
    width: 100px;
    height: 100px;
    text-align: center;
    font-size: 70px;
  }
  .keyboard {
    display: flex;
    flex-wrap: wrap;
    width: 900px;
    margin-top: 100px;
  }
  .key {
    width: auto;
    height: auto;
    margin: 10px 10px;
    padding: 3px 3px;
    font-size: 35px;
    text-align: center;
    line-height: 40px;
    cursor: pointer;
  }
  .key:hover {
    background-color: rgb(211, 211, 211);
    transform: scale(1.5);
  }
  .clicked_key {
    width: 40px;
    height: 40px;
    margin: 10px 10px;
    padding: 3px 3px;
    font-size: 35px;
    text-align: center;
    line-height: 40px;
    background-color: rgb(211, 211, 211);
  }
  .lives {
    display: flex;
    margin-top: 25px;
    margin-left: 25px;
    align-items: center;
    align-self: flex-start;
    border: 1px solid black;
  }
  .lives_title {
    margin: 0 10px;
    font-size: 15px;
    text-transform: uppercase;
    font-weight: bold;
  }
  .lives_content {
    margin-right: 10px;
    font-size: 18px;
    font-weight: bold;
    color: red;
  }
  .lose {
    position: absolute;
    width: 100%;
    height: 100%;
    left: 0;
    top: 0;
    display: flex;
    flex-direction: column;
    align-items: center;
    background-color: rgba(197, 200, 0, 0.9);
  }
  .logo_lose {
    background-image: url("./you_lose.png");
    background-size: contain;
    background-position: center;
    width: 60%;
    height: 60%;
    background-repeat: no-repeat;
  }
  .lose_tryagain {
    font-size: 20px;
    padding: 10px;
    cursor: pointer;
    text-transform: uppercase;
    border: 1px solid black;
    font-weight: bold;
    transition: 100ms linear all;
  }
  .lose_tryagain:hover {
    background-color: black;
    color: rgba(197, 200, 0, 0.9);
  }
  .win {
    position: absolute;
    width: 100%;
    height: 100%;
    left: 0;
    top: 0;
    display: flex;
    flex-direction: column;
    align-items: center;
    background-color: rgba(197, 200, 0, 0.9);
  }
  .logo_win {
    background-image: url("./you_win.png");
    background-size: contain;
    background-position: center;
    width: 60%;
    height: 60%;
    background-repeat: no-repeat;
  }
  .win_replay {
    font-size: 20px;
    padding: 10px;
    cursor: pointer;
    text-transform: uppercase;
    border: 1px solid black;
    font-weight: bold;
    transition: 100ms linear all;
  }
  .win_replay:hover {
    background-color: black;
    color: rgba(197, 200, 0, 0.9);
  }
}
